import React, { useEffect, useState } from 'react';
import { useLiff } from 'react-liff';
import {Container, Navbar, Nav} from "react-bootstrap";
import {Button, Col, Row, Card} from "react-bootstrap";

import './App.css';

function Loader() {
    return(
        <>
            <div className="loader text-center">
                <div className="loader-inner">
                    <div className="lds-roller mb-3">
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                    </div>
                    <h4 className="text-uppercase font-weight-bold">Loading</h4>
                </div>
            </div>
        </>
    );
}

function App() {
    function GetRemindList() {
        const [remind, setRemind] = useState({});
        const [type, setType] = useState('');
        const [isLoading, setIsLoading] = useState(true);
        const [isError, setIsError] = useState(false);
        const [sheetId] = useState('1xyVAC8Rby3n_s5NERM2_8uPYic11YlkycDM_TknOgTs');
        const [key] = useState('AIzaSyAYfx0INy81MTWwUDbGb1iPN88g8_lnoDc');
        useEffect(() => {
            const fetchType = () =>{
                fetch('https://sheets.googleapis.com/v4/spreadsheets/' + sheetId + '/values/sheet2!A2:B10?key=' + key,
                    {
                        method: "GET",
                        headers: new Headers({
                            'Content-Type': 'application/json',
                        })
                    })
                    .then(res => res.json())
                    .then(data => {
                        setIsLoading(false);
                        setType(data.values);
                        console.log(data);
                        fetchData();
                    })
                    .catch(e => {
                        setIsLoading(false);
                        setIsError(true);
                        console.error(e);
                    });
            };
            const fetchData  = () =>{
                fetch('https://sheets.googleapis.com/v4/spreadsheets/' + sheetId + '/values/sheet1!A2:L1000?key=' + key,
                    {
                        method: "GET",
                        headers: new Headers({
                            'Content-Type': 'application/json',
                        })
                    })
                    .then(res => res.json())
                    .then(data => {
                        setIsLoading(false);
                        setRemind(data.values.filter(function (item, index, array) {
                            return item[1] == profile.userId;
                        }));
                        console.log(remind);
                    })
                    .catch(e => {
                        setIsLoading(false);
                        setIsError(true);
                        console.error(e);
                    });
            };
            fetchType();
        }, []);
        if (isLoading) {
            return <Loader/>;
        }
        if (isError) {
            return <div>Error</div>;
        }
        if(remind.length === 0){
            return <div>No any remind</div>;
        }
        return(
            <Row>
                {remind.length > 0 &&remind.map((obj, i) =>(
                    <Col md={6} sm={12} className="mb-2" key={i}>
                        <Card className="bg-white border-white border-0 shadow-sm">
                            <div className="card-custom-img" />
                            <div className="card-custom-avatar">
                                <img src={'./images/' + obj[4] + (obj[4] == 'gold' ? '.png':'.svg')} alt={obj[4]} className="img-fluid"/>
                            </div>
                            <Card.Body>
                                <Row>
                                    <Col></Col>
                                    <Col>{type.find(function (item, index, array){
                                        return item[0] === obj[2];
                                    })[1]}
                                    </Col>
                                </Row>
                                <Row>
                                    <Col></Col>
                                    <Col>{type.find(function (item, index, array){
                                        return item[0] === obj[3];
                                    })[1]}
                                    </Col>
                                </Row>
                                <Row>
                                    <Col></Col>
                                    <Col>{obj[4]}</Col>
                                </Row>
                                <Row>
                                    <Col></Col>
                                    <Col>{obj[5]}</Col>
                                </Row>
                                <Row>
                                    <Col></Col>
                                    <Col>{obj[6]}</Col>
                                </Row>
                                <Row>
                                    <Col></Col>
                                    <Col>{obj[7]} {obj[8]} {obj[9]}</Col>
                                </Row>
                            </Card.Body>
                            <Card.Footer style={{background: 'inherit', borderColor: 'inherit'}}>
                                <Row>
                                    <Col className="d-grid gap-2">
                                        <Button variant="warning" className="rounded-pill"></Button>
                                    </Col>
                                    <Col className="d-grid gap-2">
                                        <Button variant="danger" className="rounded-pill"></Button>
                                    </Col>
                                </Row>
                            </Card.Footer>
                        </Card>
                    </Col>
                ))}
            </Row>
        );
    }

    const [profile, setProfile] = useState({
        "userId": "",
        "displayName": "",
        "pictureUrl": "",
        "statusMessage": "",
    });
    const { error, liff, isLoggedIn, ready } = useLiff();

    useEffect(() => {
        if (!isLoggedIn) return;
        (async () => {
            const profile = await liff.getProfile();
            setProfile({
                "userId": profile.userId,
                "displayName": profile.displayName,
                "pictureUrl": profile.pictureUrl,
                "statusMessage": profile.statusMessage,
            });
        })();
    }, [liff, isLoggedIn]);

    const LoginLiff = () => {
        if (error) return <p>Something is wrong.</p>;
        if (!ready) return <Loader/>;
        if (!isLoggedIn) {
            return (
                <>
                    <Card>
                        <Card.Body>
                            <Card.Img variant="top" src={'./images/logo.png'} />
                        </Card.Body>
                        <Card.Footer>
                            <div className="d-grid gap-2">
                                <Button variant="primary" onClick={liff.login}>Login</Button>
                            </div>
                        </Card.Footer>
                    </Card>
                </>
            );
        }
        return (
            <>
                <h3>Welcome, {profile.displayName}!</h3>
                <GetRemindList></GetRemindList>
                {/*<Button className="btn btn-light" onClick={liff.logout}>Logout</Button>*/}
            </>
        );
    };

    return (
        <Container>
            <Row className="mb-5">
                <Col>{LoginLiff()}</Col>
            </Row>
            <Navbar fixed="bottom" bg="light">
                <Container fluid>
                    <Col sm={4} className="text-center">
                            <Nav.Link href="#features">RemindList</Nav.Link>
                    </Col>
                    <Col sm={4} className="text-center">
                        <Nav.Link href="#features">AddRemind</Nav.Link>
                    </Col>
                    <Col sm={4} className="text-center">
                        <Nav.Link href="#features">Subscription</Nav.Link>
                    </Col>
                </Container>
            </Navbar>
        </Container>
    );
}

export default App;
